import React, { PureComponent } from "react";
import Input from "./input";
import TextArea from "./textarea";
import Dropzone from "react-dropzone";
import SelectInput from "./selectInput";
import Checkbox from "./checkbox";
import Joi from "joi-browser";
import _ from "lodash";
class Form extends PureComponent {
  constructor(props) {
    super(props);
    this.state = { data: {}, errors: {} };
    this.onMouseEnter = this.onMouseEnter.bind(this);
  }

  onMouseEnter(event) {
    let values = { ...this.state.data };
    const { nand, disabled } = this.state;
    values = _.chain(values)
      .pick(nand)
      .pickBy(v => v !== "")
      .value();
    if (_.isEmpty(values)) {
      const { childNodes } = event.currentTarget;
      const nodes = [...childNodes];
      const input = nodes.find(el => el.name !== undefined);
      const inputName = nand.find(el => el === input.name);
      const disable = nand.filter(el => el !== inputName);
      let data = { ...disabled };
      data[input.name] = false;
      disable.forEach((el, i) => {
        data[el] = true;
      });
      this.setState({ disabled: data });
    }
  }

  handleSubmit = e => {
    e.preventDefault();
    const errors = this.validate();
    this.setState({ errors: errors || {} });
    if (errors) return;
    this.doSubmit();
  };

  handleChange = ({ currentTarget: input }) => {
    let errors = { ...this.state.errors };
    const errorMessage = this.validateProperty(input);
    if (errorMessage) errors[input.name] = errorMessage;
    else delete errors[input.name];
    const data = { ...this.state.data };
    if (input.type === "checkbox") {
      data[input.name] = input.checked;
    } else {
      data[input.name] = input.value;
    }
    if (
      this.state.nand !== undefined &&
      this.state.nand.indexOf(input.name) > -1 &&
      !_.isEmpty(errors)
    ) {
      errors = { ...this.validate(data) };
    }
    this.setState({ data, errors });
  };

  validateProperty = ({ name, value, checked, type }) => {
    let data;
    if (type === "checkbox") {
      data = checked;
    } else {
      data = value;
    }
    const obj = { [name]: data };
    const schema = { [name]: this.config[name] };
    const { error } = Joi.validate(obj, schema);
    return error ? error.details[0].message : null;
  };

  validate = data => {
    const options = {
      abortEarly: false
    };
    const { error } = Joi.validate(
      { ...this.state.data, ...data },
      this.schema,
      options
    );
    if (!error) return null;
    const errors = {};
    for (let item of error.details) {
      errors[item.path[0]] = item.message;
      if (item.path.length == 0) {
        item.context.peers.forEach(el => {
          errors[el] = item.message;
        });
      }
    }
    return errors;
  };

  renderInput = (name, label, desc, type = "text") => {
    const { errors, data, disabled } = this.state;
    return (
      <Input
        className={`${disabled[name] ? "disabled" : ""} ${
          errors[name] ? "error" : ""
        }`}
        disabled={disabled[name] ? true : false}
        label={label}
        name={name}
        desc={desc}
        type={type}
        value={data[name]}
        onChange={this.handleChange}
        onMouseEnter={this.onMouseEnter}
        error={errors[name]}
      />
    );
  };

  renderTextArea = (name, label, desc, type = "text") => {
    const { errors, data, disabled } = this.state;
    return (
      <TextArea
        className={`${disabled[name] ? "disabled" : ""} ${
          errors[name] ? "error" : ""
        }`}
        disabled={disabled[name] ? true : false}
        label={label}
        name={name}
        desc={desc}
        type={type}
        value={data[name]}
        onChange={this.handleChange}
        error={errors[name]}
      />
    );
  };

  renderSelectInput = (name, label, desc, options) => {
    const { errors, data, disabled } = this.state;
    return (
      <SelectInput
        className={`${disabled[name] ? "disabled" : ""} ${
          errors[name] ? "error" : ""
        }`}
        disabled={disabled[name] ? true : false}
        label={label}
        value={data[name]}
        desc={desc}
        name={name}
        options={options}
        onChange={this.handleChange}
        onMouseEnter={this.onMouseEnter}
        error={errors[name]}
      />
    );
  };

  renderCheckbox = (name, label, type = "checkbox") => {
    const { errors, data, disabled } = this.state;
    return (
      <Checkbox
        className={`${disabled[name] ? "disabled" : ""} ${
          errors[name] ? "error" : ""
        }`}
        label={label}
        name={name}
        type={type}
        value={data[name]}
        onChange={this.handleChange}
        error={errors[name]}
      />
    );
  };

  renderButton = label => {
    return <button className="ui blue">{label}</button>;
  };

  renderDropZone = label => {
    const { errors, data } = this.state;
    return (
      <Dropzone onDrop={acceptedFiles => console.log(acceptedFiles)}>
        {({ getRootProps, getInputProps, acceptedFiles }) => (
          <section>
            <div className="dropzone ui select" {...getRootProps()}>
              <input {...getInputProps()} />
              <p>{label}</p>
            </div>
          </section>
        )}
      </Dropzone>
    );
  };
}

export default Form;
