import React from "react";

const Checkbox = ({ name, label, error, className, ...rest }) => {
  return (
    <div className={`ui checkbox ${className}`}>
      <input type="checkbox" {...rest} name={name} />
      <label htmlFor={name} className="ui">
        {label}
      </label>
      {error && <p className="errorMsg">{error}</p>}
    </div>
  );
};

export default Checkbox;
